@font-face {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 100;
    src: url("fonts/Roboto-Light.ttf") format('ttf');
}
@font-face {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: normal;
    src: url("fonts/Roboto-Regular.ttf") format('ttf');
}
@font-face {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 500;
    src: url("fonts/Roboto-Medium.ttf") format('ttf');
}

@font-face {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: bold;
    src: url("fonts/Roboto-Bold.ttf") format('ttf');
}

html {
    -webkit-tap-highlight-color : rgba(0, 0, 0, 0);
}
body {
    z-index: 1000;
    font-family: 'Roboto';
    font-size: 'normal';
    line-break: anywhere;
}

/* All */
.MuiMobileStepper-dot {width: 4px !important; height: 4px !important;}
.MuiMobileStepper-dotActive {background-color: #5093FF !important;}
.cancle_btn {background-color: rgba(0, 0, 0, 0.12) !important; color: #FFFFFF !important; width: 100%;  height: 45px;}
.select_btn {background-color: #5093FF !important; color: #FFFFFF !important; width: 100%; height: 45px;}
.select_btn:disabled { background-color: rgba(80, 147, 255, 0.5) !important; }
.MuiChip-outlinedPrimary { color: #5093FF !important; border: 1px solid #5093FF !important}
.MuiCircularProgress-colorPrimary { color: #5093FF !important;}
.MuiFab-primary { background-color: #5093FF !important;}
.avatar30 {width: 30px !important; height: 30px !important; text-align: left !important;}
.hashtag_img { padding-left: 30px; padding-top: 5px; display: inline; background-image: url('img/tag_img.png'); background-size: 100%; background-repeat: no-repeat; background-position: center center;}
.otherGoodsTag_img { padding-left: 30px; padding-top: 20px; display: inline; background-image: url('img/otherGoodsTag_img.png'); background-size: 100%; background-repeat: no-repeat; background-position: center center; position: absolute;}
.noneGoodsTag_img { padding-left: 30px; padding-top: 20px; display: inline; background-image: url('img/noneGoodsTag_img.png'); background-size: 100%; background-repeat: no-repeat; background-position: center center; position: absolute;}
.MuiBackdrop-root { background-color: rgba(0, 0, 0, 0.6) !important;}

/* bottomNavbar */
.bottomNavbar {
    box-shadow: 0px 8px 10px rgba(0, 0, 0, 0.14), 0px 3px 14px rgba(0, 0, 0, 0.12), 0px 5px 5px rgba(0, 0, 0, 0.2);
    width: 100%; height: 10%; background-color: #FFFFFF;
}

/* Main */
.main_content { margin-top: 20px; padding: 0px 10px;}
.main_content:first-child { margin-top: 10px; }
.main_content:nth-child(2) { margin-top: 6px;}
.main_content:nth-child(3) { margin-top: 6px;}
.main_content_background { background-color: #FFFFFF; filter: drop-shadow(0px 1px 5px rgba(0, 0, 0, 0.15));}
.main_board_title { text-align: center;}
.main_board_title div { border: 1px solid rgba(0, 0, 0, 0.06); border-radius: 10px; padding: 10px; text-align: center; line-height: 100%; background-color: #FFFFFF; width: 30px; height: 30px; box-sizing: content-box; margin: auto;}
.main_board_title img{ width: 30px !important; height: 30px !important; line-height: 40px; }
.icon_shadow {filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));}
.main_board_title p {margin: 3px; font-size: 12px; white-space: nowrap; overflow: hidden;}
.main_event_group { width: 100% }
.main_event_group img { width: 100%;}
.main_event_group p { width: 100%; text-align: center; margin: 0px; margin-top: -3px;}
.main_event_group b { color: rgba(0, 0, 0, 0.5); padding: 0px; font-size: 20px;}
.main_content_title { padding: 10px 0px; font-size: 18px; width: 100%;}
.main_margin { margin-bottom: 10px;}
.main_board_create_icon { width: 20px; height: 20px;}
.main_board_create_icon > svg {width: 20px; height: 20px;}

/* Etc List */
.etc_company {font-size: 10px; color: rgba(0, 0, 0, 0.5) !important;}

/* Etc FAQ */
.faq_menu{ -ms-overflow-style: none; scrollbar-width: none; overflow-x: auto; -ms-user-selEtc: none; -moz-user-select: -moz-none; -webkit-user-select: none; -khtml-user-select: none; user-select:none;}
.faq_menu::-webkit-scrollbar{ display:none; }
.faq_table { text-align: center; margin: 10px 0px; }
.faq_td { min-width: 95px; height: 40px; vertical-align: middle; cursor: pointer; -webkit-tap-highlight-color : rgba(0, 0, 0, 0) !important;}
.faq_td div { min-width: 90px; height: 35px; font-size: 14px; font-weight: 400 }

/* Etc Event */
.event_list { margin: 5px 20px }
.event_list a:hover { text-decoration: none;}
.event_list img { width: 100%; margin-top: 5px; border-radius: 10px;}
.event_list p { text-align: right; margin: 0px; color: rgba(0, 0, 0, 0.4); font-size: 14px; font-weight: 900; }

/* User Login */
.login_logo { margin-top: 30px !important; text-align: center;}
.login_logo img { width: 100%;}
.login_logo p { font-size: 22.6px; padding-left: 15px;}
.login_comment { margin-top : 40px !important; text-align: center; color: rgba(0, 0, 0, 0.5); font-size: 14px;}
.login_button_group {margin-top : 20px !important}
.login_button { width: 100%; margin-bottom: 6px; }
.login_button:hover {cursor: pointer;}
.login_info { margin-top:100px; margin-bottom: 30px; padding: 0px; color: #000000; text-align: center; font-size: 12px; }
.login_info li:before{ content: "|"; padding-right: 5px;}
.login_info li:first-child:before{ content: ""; }
.login_info li{ list-style: none; display: inline-block; padding-left: 5px }

/* User Regist */
.regist_form { padding-top: 40px; min-height: 450px;}
.regist_content { text-align: left; margin-top: 50px !important}
.regist_content p {margin: 0px; font-size: 14px; font-weight: 400; color: rgba(0, 0, 0, 0.5)}
.regist_button { padding: 10px; }
.regist_button:first-child {padding-left: 8%;}
.regist_button:last-child {padding-right: 8%;}
.regist_agree_text { margin-left: -25px; font-size: 16px;}
.regist_agree_text span{ display: inline-block; }
.regist_agree_text p{ display: inline-block; font-size: 12px; padding-left: 5px; color: #5093FF;}
.regist_agree_view { color: #000000 !important; font-size: 12px; text-decoration: revert !important; }
.regist_info_input { width: 100%; height: 50px; background-color: #F6F6F6; border-radius: 4px; border: 1px solid #E8E8E8; padding-left: 10%; }
.regist_info_input:focus { outline:none ;}
.regist_info_input::placeholder { color: #BDBDBD }
.regist_info_nickname_error {color: red; font-size: 12px;}
.regist_info_button_group { border: 1px solid #E8E8E8; background-color: #F6F6F6 ; border-radius: 4px ;}
.regist_info_button { height: 50px; width: 50%; background-color: #F6F6F6 ; border: 0px;}
.regist_info_button:focus { outline:none ;}
.regist_info_button:hover { cursor: pointer; }
.regist_info_select_title { margin-top: 30px; font-size: 18px;}
.regist_info_select_group { margin-top: 10px !important; font-size: 14px;}
.regist_info_select { width: 80%; height: 50px; background-color: #F6F6F6; border-radius: 4px; border: 1px solid #E8E8E8; padding-left: 10%; }
.regist_info_select_text { width: 20% ; padding-left: 5px;}
.regist_info_select:focus { outline:none ;}
.regist_info_sport {margin-right: 20px !important; margin-top: 20px !important;}
.regist_progress { text-align: center !important;}
.regist_sport_btn_group > div { padding-bottom: 20px; border-bottom: 1px solid rgba(0, 0, 0, 0.2);}
.regist_sport_favorite_btn_group { margin-bottom: 10px !important; }
.MuiStepLabel-active {font-weight: 400 !important;}
.MuiStepLabel-completed {font-weight: 400 !important;}
.MuiStepIcon-active {color: #5093FF !important;}
.MuiStepIcon-completed {color: #5093FF !important;}

/* Card */
.card_user_nick_name { font-size: 14px; font-weight: 700; margin-left: 8px; }
.card_user_nick_name > div {display: inline;}
.card_user_nick_name > span {font-size: 12px; font-weight: 400; margin-left: 8px; color: rgba(80, 147, 255, 0.8);}
.card_user_nick_name > p {margin: 0px; font-size: 14px; color: rgba(0, 0, 0, 0.5); font-weight: 400; white-space: nowrap;}
.card_content { padding-top: 0px !important; padding-bottom: 10px !important; }
.card_content_title { font-size: 14px; font-weight: 700; margin-bottom: 15px;}
.card_content_content { font-size: 14px; margin-bottom: 15px;}
.card_content_icon_group { margin-top:15px !important; border-top: 1px solid rgba(0, 0, 0, 0.2) !important; padding: 0px 10px;}
.card_content_icon svg {color: #5093FF; width: 22px !important; height: 22px !important;}
.card_content_count {text-align: right; color: rgba(0, 0, 0, 0.34); font-size: 14px;}
.card_content_count svg {margin-left: 10px; margin-right: 5px;}
.content_hashtag { margin-top: 10px;}
.content_hashtag a { color: #5093FF; font-size: 14px; margin-right: 10px;}

.content_full_group { padding-bottom: 30px; }
.content_full_img img { width: 100%; border-radius: 10px; }
.content_full_title { margin-top: 9px; font-size: 14px; font-weight: 700;}
.content_full_title a { text-decoration: none; color: #000000; }
.content_full_title a:hover { text-decoration: none; }
.content_full_content {font-size: 14px; font-weight: 400; color: rgba(0, 0, 0, 0.5);}

.content_half_group { padding:0px 5px 20px 5px; text-align: center;}
.content_half_group img {width: 100%;}
.content_half_title { text-align: left; font-weight: 700; font-size: 14px; color: #000000;}
.content_half_date { text-align: left; font-weight: 400; font-size: 14px; color: rgba(0, 0, 0, 0.5);}
.content_half_profile {text-align: left; font-weight: 400; font-size: 14px; color: #000000;}


.content_half_text_group { padding-bottom: 20px;}
.content_half_text_group > div:first-child { position: relative; padding-bottom: 41.666666667%;}
.content_half_text_group > div:first-child > img { width: 100%; height: 100%; position: absolute; border-radius: 10px; border: 1px solid rgba(0, 0, 0, 0.06);}
.content_half_text_right { padding-left: 15px; margin-bottom: auto !important;}
.content_half_text_profile { padding-bottom: 10px;}
.content_half_text_profile span { font-size: 14px; padding-left: 10px; font-weight: 700;}
.content_half_text_profile img {width: 30px; height: 30px; border-radius: 15px; filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));}
.content_half_text_title {font-size: 14px; font-weight: 400; padding-bottom: 10px;}
.content_half_text_another { padding-top: 10px; color: rgba(0, 0, 0, 0.5); font-size: 12px; }
.content_half_text_next { text-align: right; color: #D5D5D5; height: 100%;}

.content_list_slide{ -ms-overflow-style: none; scrollbar-width: none; overflow-x: auto; -ms-user-selEtc: none; -moz-user-select: -moz-none; -webkit-user-select: none; -khtml-user-select: none; user-select:none;}
.content_list_slide::-webkit-scrollbar{ display:none; }
.content_list_table { margin: 10px 0px; }
.content_list_td { min-width: 210px; vertical-align: middle; padding: 0px 5px;}
.content_list_td:first-child {padding-left: 0px;}
.content_list_td img {border: 1px solid rgba(0, 0, 0, 0.06); border-radius: 10px; width: 100%;}
.content_list_title { font-size: 14px; font-weight: 700; }
.content_list_content { width: 200px; font-size: 14px; font-weight: 400; white-space: nowrap; overflow: hidden; text-overflow: ellipsis; white-space: normal; line-height: 1.2; height: 2.2em; text-align: left; word-wrap: break-word; display: -webkit-box; -webkit-line-clamp: 2; -webkit-box-orient: vertical;}

/* Board Kind */
.board_kind_list { padding: 10px; }
.board_kind_list > div { position: static; margin-bottom: 20px; color: #FFFFFF;}
/*.board_kind_list > div:last-child {color: #000000}*/
.board_kind_img { cursor: pointer; text-decoration: none; width: 100%; position: relative; padding-bottom: 58.823%;}
.board_kind_img > img { width: 100%; position: absolute; border-radius: 20px; height: 100%; filter: brightness(66%);}
.board_kind_img > div { width: 100%; position: absolute; z-index: 2; height: 100%; text-align: center; padding-top: 15%; font-size: 16px; line-height: 24px; text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);}
.board_kind_img > div > p {font-weight: 700; font-size: 20px; margin: 16px;}

/* Board List */
.board_list_top { text-align: center; margin: 20px 0px;}
.board_search { width: 80%; height: 50px; font-size: 16px; background-color: #F6F6F6; border: 1px solid #E8E8E8; border-radius: 100px; padding-left: 16px; margin-bottom: 20px;}
.board_search::placeholder { color: #BDBDBD; font-weight: 500; }
.board_search:focus { outline:none ;}
.board_banner {width: 100%; }
.board_list {padding: 0px 10px;}
.board_list_content {padding-bottom: 20px;}
.board_create {position: fixed !important; bottom:0; right: 0; margin: 30px !important;}
.board_top { margin-bottom: 20px !important; padding: 0px 10px; font-size: 18px;}
.board_keyword { font-size: 18px; }
.board_keyword_table{ -ms-overflow-style: none; scrollbar-width: none; overflow-x: auto; -ms-user-selEtc: none; -moz-user-select: -moz-none; -webkit-user-select: none; -khtml-user-select: none; user-select:none;}
.board_keyword_table::-webkit-scrollbar{ display:none; }
.board_keyword_table table { padding: 0px; margin: 15px 0px 20px 0px; border-collapse: separate; border-spacing: 10px 0px;}
.board_keyword_table td { padding: 0px; min-width: 60px; height: 30px; text-align: left;}
.board_keyword_table div { text-align: center;  background-color: #FFFFFF; font-size: 14px; color: #5093FF; height: 30px; line-height: 30px; display: inline; overflow: hidden; white-space: nowrap;}
.board_another { text-align: left; color: rgba(0, 0, 0, 0.5); font-size: 12px;}
.board_sort { text-align: right;}
.board_sort select { border: 0px;  font-size: 12px; background-color: #FFFFFF;}
.board_sort select:focus { outline: none;}


/* Comment */
.content_comment { padding: 10px;}
.content_recomment { margin-left: 30px;}
.content_recomment_column { margin-top: 5px; background-color: rgba(80, 147, 255, 0.1); padding: 10px;}
.content_comment_profile_img { width: 30px; height: 30px; filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25)); border-radius: 15px;}
.content_comment_profile { padding-left: 10px; margin-bottom: 5px !important;}
.content_comment_profile span {font-weight: 700; font-size: 12px;}
.content_comment_content { padding-left: 10px;}
.content_comment_content p {font-weight: 400; font-size: 12px; margin: 0px}
.content_comment_content span {font-weight: 400; font-size: 12px; color: rgba(0, 0, 0, 0.34);}
.content_comment_recomment_id { color: #5093FF !important; margin-right: 5px !important;}
.content_comment_content a { color: rgba(0, 0, 0, 0.34)}
.comment_rating {padding-left: 20px; padding-bottom: 0px;}
.comment_rating > svg {width: 15px; height: 15px; color: #5093FF;}

.comment_create_form {font-size: 14px; position: fixed; bottom:0; align-self:flex-end; background-color: #FFFFFF; padding: 10px; border-top: 1px solid rgba(0, 0, 0, 0.2);}
.comment_create_form div { margin-top: auto;}
.comment_create_form img {height: 30px; width: 30px; border-radius: 15px; filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));}
.comment_create_form_content {position: relative;}
.comment_create_form_content div {position: absolute; top: -24px; background-color: #5093FF; color: #FFFFFF;}
.comment_create_form_content div > svg {color: #FFFFFF}
.comment_create_form_content textarea { width: 100%; border: 0px; padding-top: 5px; resize: none; display: inline-block; overflow:hidden;}
.comment_create_form_content textarea:focus { outline: none;}
.comment_create_form_content textarea:disabled { background-color: #FFFFFF;}
.comment_create_form_content::placeholder { color: rgba(0, 0, 0, 0.2); }
.comment_create_form button { width: 100%; color: rgba(80, 147, 255, 1); border: 0px; background-color: #FFFFFF; text-align: right;}
.comment_create_form button:focus { outline: none;}
.comment_create_column_title { font-size: 24px; font-weight: 700; text-align: center;}
.comment_create_column_close {line-height: 100%;}
.comment_create_column_close button {padding: 0px;}
.comment_create_column_content { text-align: center; font-size: 14px; color: rgba(0, 0, 0, 0.5);}
.comment_create_column_content p { margin: 0px 0px 10px 0px; color: #5093FF; font-size: 14px;}
.comment_create_column_text { width: 100%; margin-top: 10px; text-align: center; border: 1px solid rgba(0, 0, 0, 0.12); border-radius: 10px;}
.comment_create_column_text textarea { width: 90%; padding: 10px; border: 0px; resize: none;}
.coment_create_column_button {width: 80px; height: 30px; color: #FFFFFF; background-color: #5093FF; border: 0px; font-size: 18px; border-radius: 4px;}
.coment_create_column_button:disabled { background-color: rgba(80, 147, 255, 0.5) !important; filter: none; }
.coment_cancle_column_button {width: 80px; height: 30px; color: #515151; background-color: #FFFFFF; border: 0px; font-size: 18px; border-radius: 4px;}

/* Board Create */
.board_create_body { margin-top:  30px !important; }
.board_create_title { font-size: 18px; padding-left: 20px;}
.board_create_form_group { margin-bottom: 20px !important;}
.board_create_select { width: 106%; height: 40px; padding-left: 8px; border: 1px solid rgba(0, 0, 0, 0.12); border-radius: 8px;  appearance : none ; -webkit-appearance : none ; background: url('./img/select_arrow.png') 90.0% center no-repeat;}
.board_create_select::-ms-expand { display: none;}
.board_create_input { width: 100%; height: 35px;  padding-left: 10px; border: 1px solid rgba(0, 0, 0, 0.12); border-radius: 8px; box-sizing: content-box;}
.board_create_form::placeholder { color: rgba(0, 0, 0, 0.34);}
.board_create_form:focus{ outline: none;}
.board_create_file_before { width: 100%; padding-bottom: 100%; background-color: rgba(0, 0, 0, 0.06); position: relative;}
.board_create_file {width: 100%; text-align: center; position: absolute; padding-top: 40%;}
.board_create_file p {font-size: 14px; font-weight: 700; color: rgba(0, 0, 0, 0.5); margin: 0px 0px 17px 0px;;}
.board_create_file label { width: 160px; height: 30px; background-color: rgba(0, 0, 0, 0.34); color: #FFFFFF; border-radius: 4px; border: 0px; padding: 5px 40px; font-size: 14px;}
.board_create_file_after { width: 100%; padding-bottom: 100%; background-color: #FFFFFF; position: relative; background-repeat: no-repeat; background-size: cover; background-position: center center;}
/* .board_create_file_after img { width: 100%; height: 100%; position: absolute; } */
.board_create_file_after_button { text-align: right; width: 100%; padding-right: 20px;}
.board_create_file_after_button button { font-size: 12px; width: 80px; height: 30px; background-color: rgba(0, 0, 0, 0.34); color: #FFFFFF; border-radius: 4px; border: 0px; margin-top: 10px;}
.board_create_file_after_button button:nth-child(2) {margin-left: 10px; background-color: #EB3F3F; width: 60px;}
.board_create_file_after div { width: 15px; height: 15px; background-color: #5093FF; color: #FFFFFF; border-radius: 7.5px; position: absolute; text-align: center; font-size: 12px; line-height: 17px;}
.board_create_record { text-align: right; width: 100%;}
.board_create_record_form { margin-bottom: 10px !important; padding: 0px 20px;}
.board_create_record p { text-align: left; padding-left: 20px; font-size: 14px; font-weight: 700;}
.board_create_record input {width: 90%; padding: 0px; padding-left: 10px; height: 30px; border: 1px solid rgba(0, 0, 0, 0.12);}
.board_create_content { margin-top: 20px; width: 100%; text-align: center; padding: 0px 20px;}
.board_create_content div { border: 1px solid rgba(0, 0, 0, 0.12); border-radius: 8px; }
.board_create_content textarea { width: 90%; padding: 10px 0px 0px 0px; border: 0px; resize: none;}
.board_create_keyword { text-align: left; width: 100%; padding: 0px 20px; margin-bottom: 20px;}
.board_create_keyword_title {font-size: 14px; color: rgba(0, 0, 0, 0.34); margin: 5px 0px;}
.board_create_keyword_content {font-size: 12px; color: rgba(0, 0, 0, 0.5); background-color: rgba(0, 0, 0, 0.06);border-radius: 2px; padding: 3.3px 4.5px; margin:0px 10px 10px 0px; display: inline-block;}
.board_create_button { margin-top: 30px; width: 100%; padding: 0px 20px;}
.board_create_button button { width: 100%; background-color: #5093FF; color: #FFFFFF; border-radius: 4px; height: 50px; border: 0px; filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25)); font-size: 16px;}
.board_create_button button:disabled { background-color: rgba(80, 147, 255, 0.5) !important; filter: none;}
.board_create_file_after_point { padding: 0px 20px; margin-top: 10px !important;}
.board_create_file_after_point_circle {  width: 15px; height: 15px; background-color: #5093FF; color: #FFFFFF; border-radius: 7.5px; text-align: center; font-size: 12px; line-height: 17px;}
.board_create_file_after_point_input { width: 90%; font-size: 12px; height: 30px; padding: 0px 0px 0px 10px; border-radius: 8px; border: 1px solid rgba(0, 0, 0, 0.12);}
.board_create_file_another { width: 100%; padding-bottom: 30%; background-color: rgba(0, 0, 0, 0.03); position: relative; margin-bottom: 20px;}
.board_create_file_another_form {width: 100%; text-align: center; position: absolute; padding-top: 5%;}
.board_create_file_another_form p {font-size: 12px; font-weight: 700; color: rgba(0, 0, 0, 0.34); margin: 0px 0px 17px 0px;;}
.board_create_file_another_form label { width: 160px; height: 30px; background-color: rgba(0, 0, 0, 0.34); color: #FFFFFF; border-radius: 4px; border: 0px; padding: 5px 40px; font-size: 14px;}



/* Dialog Keyword */
.dialog_keyword_body { padding-bottom: 50px }
.dialog_keyword_input { margin-top: 80px; border: 1px solid rgba(0, 0, 0, 0.12); border-left: 0px; border-right: 0px; padding: 10px 5px; }
.dialog_keyword_input input { width: 75%; border: 0px; padding-left: 10px;}
.dialog_keyword_input button { width: 20%; background-color: #FFFFFF; border: 0px; font-size: 14px; color: rgba(80, 147, 255, 0.7); text-align: right;}
.dialog_keyword_content { padding: 10px}

/* Board Content Play */
.board_play { margin-bottom: 20px; box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.15); border-radius: 10px; background-color: #FFFFFF;}
.board_play_profile { font-size: 14px; font-weight: 700; padding: 10px; width: auto;}
.board_play_profile img { width: 30px; height: 30px; border-radius: 15px; filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25)); margin-right: 7px;}
.board_play_profile > .play_profile_text { margin-right: auto; }
.board_play_profile > div > div { display: inline; }
.board_play_profile > div > span {font-size: 12px; font-weight: 400; margin-left: 8px; color: rgba(80, 147, 255, 0.8);}
.board_play_profile > div > p {margin: 0px; font-weight: 400; color: rgba(0, 0, 0, 0.5); font-size: 14px; width: 100%; white-space: nowrap;}
.board_play_img { width: 100%; padding-bottom: 100%; position: relative; margin-bottom: 10px; background-position: center center; background-repeat: no-repeat; background-size: cover; border-top: 1px solid rgba(0, 0, 0, 0.06); border-bottom: 1px solid rgba(0, 0, 0, 0.06);}
/* .board_play_img img {width: 100%; height: 100%; position: absolute; border-top: 1px solid rgba(0, 0, 0, 0.06); border-bottom: 1px solid rgba(0, 0, 0, 0.06);} */
.board_play_goods { -ms-overflow-style: none; scrollbar-width: none; overflow-x: auto; -ms-user-selEtc: none; -moz-user-select: -moz-none; -webkit-user-select: none; -khtml-user-select: none; user-select:none;}
.board_play_goods::-webkit-scrollbar{ display:none; }
.board_play_goods table { padding: 0px; margin-top: 5px; border-collapse: separate; border-spacing: 10px 0px;}
.board_play_goods td { padding: 0px; min-width: 55px; height: 50px; }
.board_play_goods img { width: 50px; height: 50px; border: 1px solid rgba(0, 0, 0, 0.06); border-radius: 10px;}
.board_play_hashtag { padding: 0px 10px 10px 10px; margin-top: 10px;}
.board_play_hashtag a {color: #5093FF; text-decoration: none; margin-right: 5px; font-size: 14px;}

/* Board Detail */
.board_detail { margin-top: 20px;}
.board_detail_title { padding : 0px 20px; }
.board_detail_kind { float: left; font-size: 12px; color: rgba(0, 0, 0, 0.34);}
.board_detail_report { float: right; font-size: 14px; font-weight: 700; color: rgba(0, 0, 0, 0.34)}
.board_detail_title p { clear: both; font-size: 18px; padding: 20px 0px; margin: 0px;}
.board_detail_title img {width: 30px; height: 30px; border-radius: 15px; float: left; margin-right: 10px;}
.board_detail_profile {float: left; font-size: 14px; color: rgba(0, 0, 0, 0.5);}
.board_detail_profile div:first-child {font-weight: 700; color: #000000;}
.board_detail_follow {float: right;}
.board_detail_follow button { color: #FFFFFF; border: 0px; border-radius: 4px; height: 30px;}
.board_detail_follow button:focus {outline: none;}
.board_detail_follow button.follow { background-color: #5093FF; width: 90px;}
.board_detail_follow button.update { background-color: #BDBDBD; width: 60px; margin-right: 10px;}
.board_detail_follow button.delete { background-color: #EB3F3F; width: 60px;}
.board_detail_follow button.unfollow { background-color: #FFFFFF; color: rgba(0, 0, 0, 0.5); border: 1px solid rgba(0, 0, 0, 0.12); width: 90px;}
.board_detail_bottom_button { position: fixed ; bottom: 0; background-color: #F9F9F9; width: 100%; height: 60px; text-align: center; z-index: 1500;}
.board_detail_bottom_button svg {font-size: 21px; color: #5093FF}
.board_detail_bottom_button div:nth-child(5) svg {font-size: 19px; color: #5093FF}
.board_detail_bottom_button p {text-align: left; font-size: 14px;}

    /* Play */
.board_detail_play_content_body {clear: both; padding-top: 20px; padding-bottom: 20px;}
.board_detail_play_img {width: 100%; padding-bottom: 100%; position: relative; border-top: 1px solid rgba(0, 0, 0, 0.06); border-bottom: 1px solid rgba(0, 0, 0, 0.06); background-size: cover; background-position: center center; background-repeat: no-repeat;}
/* .board_detail_play_img img {width: 100%; height: 100%; position: absolute;} */
.view_goods_btn { width: 80px; height: 25px; line-height: 25px; background-color: rgba(0, 0, 0, 0.7); color: #FFFFFF; border-radius: 4px; position: absolute; text-align: center; font-size: 12px;}
.board_detail_play_img > .otherGoods_view { background-color: rgba(255, 255, 255, 0.8); position: absolute; border: 1px solid rgba(0, 0, 0, 0.12); border-radius: 10px; width: 238px; height: 58px; padding: 10px; transform: translate(-50%, -50%); z-index: 2;}
.board_detail_play_img > .otherGoods_view img {background-color: #FFFFFF; width: 60px; height: 60px; position: static; display: inline-block; border-radius: 10px;}
.othergoods_view_goodsInfo {display: inline;}
.otherGoods_view_goodsTitle {font-size: 12px; font-weight: 700; display: inline-block;}
.otherGoods_view_goodsLink { font-weight: 900; color: rgba(0, 0, 0, 0.5); display: inline-block; line-height: 200%; margin-bottom: 5px; text-align: right;}
.otherGoods_view_goodsManufacturer {font-size: 8px; color: rgba(0, 0, 0, 0.5); margin-top: 5px !important;}
.otherGoods_view_price { font-size: 12px; color: #EB3F3F; border-top: 1px solid rgba(0, 0, 0, 0.2); padding-top: 5px;}
.board_detail_play_img > .noneGoods_view { background-color: rgba(255, 255, 255, 0.8); position: absolute; border: 1px solid rgba(0, 0, 0, 0.12); border-radius: 10px; width: 128px; height: 28px; padding: 10px; font-size: 10px; transform: translate(-50%, -50%); overflow: hidden; line-height: 18px;  z-index: 2;}

.board_detail_play_content { padding: 0px 20px 0px 20px;}
.board_detail_play_content table { margin: 10px 0px 0px 0px;}
.board_detail_play_content .board_play_hashtag { padding: 0px; margin: 10px 0px;}
.board_detail_play_content_contents { margin-top: 10px; font-size: 14px;}
.board_detail_play_comment { padding: 10px;}
.board_detail_play_add_comment { text-align: center; font-size: 14px;}
.board_detail_play_add_comment > div{padding: 10px 20px; border: 1px solid rgba(0, 0, 0, 0.2); border-left: 0px; border-right: 0px; margin-top: 20px;}
.board_detail_play_add_comment > div > div:nth-child(2){ text-align: left; color: rgba(0, 0, 0, 0.2)}
.board_detail_play_add_comment > div > div:nth-child(3){ text-align: right; color: rgba(80, 147, 255, 0.7)}
.board_detail_play_add_comment img {height: 30px; width: 30px; border-radius: 15px;}
.board_detail_play_another_goods { padding: 20px 15px;font-size: 14px; font-weight: 700;}
.board_detail_play_another_goods_title { padding: 0px 5px 10px 5px;}
.board_detail_play_another_goods > div:nth-child(2) > div { padding: 10px 5px;}
.board_detail_play_another_goods img {width: 100%; border: 1px solid rgba(0, 0, 0, 0.06); border-radius: 10px;}

    /* Column */
.board_detail_name { display: inline; width: 70%; }
.board_detail_rating { display: inline; margin-left: 20%;}
.board_detail_date { clear: both;}
.board_detail_column_sns { font-size: 14px; font-weight: 700; padding: 20px 20px 0px 20px;}
.board_detail_column_sns_title { margin-bottom: 20px}
.board_detail_column_sns_logo div {display: inline-block; width: 33.3333%;}
.board_detail_column_sns_logo div:nth-child(2) {text-align: center;}
.board_detail_column_sns_logo div:last-child {text-align: right;}
.board_detail_column_comment { margin: 20px 0px; padding: 0px 20px;}
.board_detail_column_comment button { width: 100%; background-color: #5093FF; color: #FFFFFF; border-radius: 4px; height: 50px; border: 0px; filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25)); font-size: 16px;}

/* ALRET */
.alret_action {display: inline;}
.alret_action button {background: none; border: 0px; color: #FFFFFF}
.alret_action button:focus {outline: none;}
.alret_action button:nth-child(2) {margin-left: 15px;}


/* User */
.user_profile {margin-top: -10px; position: relative; width: 100%; padding-bottom: 64%;}
.user_profile > .background_gradient {position: absolute; width: 100%; z-index: 2; background: linear-gradient(180deg, rgba(130, 130, 130, 0) 0%, #FFFFFF 100%); background-size: 100% 100%; padding-bottom: 64%; }
.user_profile > .background_img {position: absolute; width: 100%; z-index: 1; height: 100%;}
.user_profile > .follow_info {position: absolute; z-index: 4; background-color: #FFFFFF; top: 164px; width: 80%; left: 10%; box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25); border-radius: 10px; padding-top: 21px; padding-bottom: 21px; text-align: center;}
.user_profile > .follow_info > div { margin: 11px 0px}
