.user_profile > .follow_info p { margin: 0px; font-size: 14px; font-weight: 700;}
.user_profile > .follow_info span { font-size: 14px; color: rgba(0, 0, 0,0.2);}
.user_profile > .follow_button {position: absolute; top: 264px; background-color: #5093FF; color: #FFFFFF; left: 50%; transform: translate(-50%,-50%); z-index: 5; padding: 5px 0px; border-radius: 20px; min-width: 90px; text-align: center; font-size: 14px;}
.user_profile > .nomal_user { position: absolute; width: 100%;}
.nomal_user > div { position: absolute; z-index: 3; width: 90px; height: 90px; top: 34px; left: 50%; transform: translate(-50%, 0); }
