.profile_img > p > svg { font-size: 9px;}
.profile_info { background-color: rgba(255, 255, 255, 0.7); border-radius: 10px; padding: 10px 20px 0px 20px;}
.profile_info > .name { font-size: 16px; font-weight: 700; text-align: center; margin: 0px;}
.profile_info > .info {font-size: 12px; width: 50%; display: inline-block; }
.profile_info > .info > span {font-size: 14px; font-weight: 700; color: #EBB23F;}
.profile_info > .email {font-size: 10px; font-weight: 700; margin-top: 5px;}
.profile_info > .infomation {font-size: 10px; margin-top: 5px;}
.pro_sns {width: 100%; padding: 10px 20px;}
.pro_sns > div {text-align: center; font-size: 14px; font-weight: 400; border: 1px solid rgba(0, 0, 0, 0.12); border-radius: 20px; width: 30%; height: 30px; margin-bottom: 10px;}
.pro_sns > div > img {width: 16px; height: 16px; margin-left: 10px;}
.pro_sns > div > div {color: rgba(0, 0, 0, 0.5);}
.my_page { padding: 20px;}
